<template>
  <el-button @click="handleClick">{{ message }}</el-button>
</template>

<script>
export default {
  name: "MyComponent",
  data() {
    return {
      message: "Hello, world!",
    };
  },
  methods: {
    handleClick(e) {
      this.message = "Clicked!";
      this.$emit("click", e);
    },
  },
};
</script>

